import React from 'react'
import zigzag from '../block/zig-zag-paver-block-500x500.png';
import cosmic from '../block/cosmic-paver-blocks.png';
import rectangle from '../block/rectangle-paver-rb.png';
const Products = () => {
    return (
        <div className='bg-[#f0f0f0] w-full py-[50px] mt-10 rounded-[100px] font-[cursive] sm:bg-[#d3e9e9a1]' id='products'>
            
            {/* name */}
            <p className='font-bold text-[55px] text-center sm:text-[40px]'>
                <span className='border-y-[10px] border-[grey] rounded-[30px] sm:border-[blue]'>
                    Products
                </span>
            </p>

            {/* products catalog */}
            <div className="py-[40px] mt-[40px] px-4">
                <div className="max-w-[1240px] mx-auto grid grid-cols-3 gap-10 sm:flex sm:flex-col sm:gap-[4px]">

                    {/* zig zag */}
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px] " >
                        <img src={zigzag} alt="ZigZag Paver Block" className='bg-transparent h-[320px] sm:h-[250px]' loading='lazy' decoding='async'/>
                        <p className='font-[1000] text-[35px] text-[black] my-4 sm:text-[25px]'>ZigZag Paver Block</p>
                        <p className='font-[900] text-[24px] text-[black] my-4 sm:text-[20px]'>Size:<span className='font-[600] text-[white]'>80mm</span></p>
                        <p className='font-[800] text-[22px] text-[black] my-4 sm:text-[20px]'>100 Sq.ft -<span className='font-[500] text-[white]'> 285/287 Nos.</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'>Starting<span className='font-[400] text-[white] '> ₹.4300</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'><a href='#'><span className='p-2  font-[400] text-[white]  rounded-[100px] bg-[black]'>Check Now</span></a></p>
                    </div>

                    {/* Cosmic */}
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]">
                        <img src={cosmic} alt="Cosmic Paver Block" className='bg-transparent h-[320px] sm:h-[250px]' loading='lazy' decoding='async'/>
                        <p className='font-[1000] text-[35px] text-[black] my-4 sm:text-[25px]'>Cosmic Paver Block</p>
                        <p className='font-[900] text-[24px] text-[black] my-4 sm:text-[20px]'>Size:<span className='font-[600] text-[white]'>60mm</span></p>
                        <p className='font-[800] text-[22px] text-[black] my-4 sm:text-[20px]'>100 Sq.ft -<span className='font-[500] text-[white]'> 180/185 Nos.</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'>Starting <span className='font-[400] text-[white]'> ₹.3800</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'><a href='#'><span className='p-2  font-[400] text-[white]  rounded-[100px] bg-[black]'>Check Now</span></a></p>
                    </div>

                    {/* rectangle */}
                    <div className="border border-[black]  border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]">
                        <img src={rectangle} alt="Rectangle Paver Block" className='bg-transparent h-[320px] sm:h-[250px]' loading='lazy' decoding='async'/>
                        <p className='font-[1000] text-[35px] text-[black] my-4 sm:text-[25px]'>Rectangle Paver Block</p>
                        <p className='font-[900] text-[24px] text-[black] my-4 sm:text-[20px]'>Size:<span className='font-[600] text-[white]'>60mm</span></p>
                        <p className='font-[800] text-[22px] text-[black] my-4 sm:text-[20px]'>100 Sq.ft -<span className='font-[500] text-[white]'> 180/185 Nos.</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'>Starting <span className='font-[400] text-[white]'> ₹.3800</span></p>
                        <p className='font-[700] text-[19px] text-[black] my-4 sm:text-[20px]'><a href='#'><span className='p-2  font-[400] text-[white]  rounded-[100px] bg-[black]'>Check Now</span></a></p>
                    </div>

                </div>
            </div>
        </div>
    )
}


export default Products;